.video__wrapper {
  position: relative;
  width: 100%;
  height: 705px;
}

.video {
  width: 100%;
  height: 100%;
}

.video[poster] {
  height: 100%;
  object-fit: cover;
}

.video__container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.video__btn {
  // display: block;
  // position: absolute;
  // top: 50%;
  // left: 50%;
  width: 100px;
  height: 100px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  // transform: translate(-50%, -50%);
}

.video__icon-play {
  width: 70px;
  height: 70px;
  fill: @color-white;

  &:hover {
    fill: @color-light-brown;
  }
}